import React from 'react';
import logo from './logo.svg';

import { BrowserRouter as Router, Route } from 'react-router-dom';
import './App.css';
import Navbar from './components/layout/Navbar'
import Joke from './components/pages/Joke/Joke'
import JokeManual from './components/pages/Joke/JokeManual'
import About from './components/pages/About/About'
import Todo from './components/pages/Todo/Todo'
import Slider from './components/pages/Slider/Slider'

function App() {
  // function components
  return (
    <Router>
      <div className="App">
        {/* This is global components */}
        <Navbar />
      <div className="main_container">
        {/* switch route area is rerendering some area */}
        <Route path="/" exact component={Joke} />
        <Route path="/joke_manual" exact component={JokeManual} />
        <Route path="/about" exact component={About} />
        <Route path="/todo" exact component={Todo} />
        <Route path="/slider" exact component={Slider} />
      </div>



      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>

      <h1>Get started</h1>
      </div>
    </Router >
  );
}


export default App;
